button {
  appearance:none;
  border: 0;
  background-color: transparent;
}

.btn, .button {
  border: 0;
  font-family: $body-font;
  text-transform: uppercase;
  appearance: none;
  letter-spacing: 1px;
  display: inline-block;
  font-weight: 700;
  font-size: 11px;
  text-decoration: none;
  cursor: pointer;
  padding: 0 20px;
  border-radius: 30px;
  line-height: 40px;
  transition: all 0.2s ease;
  text-align: center;
  &:hover {
    background-color: $black;
    color: $white;
  }
  &.white {
    border-color: $white;
    color: $white;
    border: 2px solid $white;
    line-height: 31px;
    &:hover {
      background-color: $white;
      color: $black;
    }
  }
  &.black {
    background-color: $black;
    color: $white;
    &:hover {
      background-color: $green;
      color: $white;
      border-color: $green;
    }
  }
  &.green {
    background-color: $green;
    color: $black !important;
    &:hover {
      background-color: lighten($black,10%);
      color: $white !important;
      border-color: $white;
    }
  }
  &.big {
    padding: 12px 30px;
  }
  &.margintop {
    margin-top: 25px;
  }
  &.marginbot {
    margin-bottom: 25px;
  }
  &.tiny {
    font-size: 8px;
    line-height: 8px;
    padding: 5px 8px;
    margin-top: 5px;
    clear: both;
  }
  &.wide {
    display: block;
    width: 100%;
  }
  @include mq('phablet', max) {
    letter-spacing: 0px;
  }
}
