body {
  color: $black;
  background: $white;
  font-size: 16px;
  line-height: 1.6em;
  font-family: $body-font;
  text-size-adjust: 100%;
  position: relative;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  &.menu-open {
    overflow: hidden;
  }
}

.container {
  @include ph(2rem);
  margin: 0 auto;
  width: 100%;
  &.tiny {
    max-width: 640px;
  }
  &.article {
    max-width: 840px;
  }
  &.small {
    max-width: 1200px;
  }
  &.mid {
    max-width: 1400px;
  }
  @include mq('phablet', max) {
    @include ph(1.2rem);
  }
}

.clear {
  @include clearfix;
}

.center {
  text-align: center;
}

a {
  color: $black;
  text-decoration: none;
  outline: 0;
  transition: all 0.2s ease;
  &:hover {
    color: $darkgreen;
  }
}

h1,
h2,
h3,
h4,
h5,
.h1,
.h2,
.h3,
.h4,
.h5 {
  font-family: $heading-font;
  letter-spacing: 0;
  font-weight: 600;
  color: $black;
  margin-bottom: 10px;
  line-height: 1.2;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  font-weight: inherit;
  color: $green;
  text-decoration: underline;
}
h1 {
  font-size: 28px;
  @include mq('tablet-small', max) {
    font-size: 24px;
    line-height: normal;
  }
}
h2 {
  font-size: 22px;
  @include mq('tablet-small', max) {
    font-size: 20px;
    line-height: normal;
  }
}
h3 {
  font-size: 20px;
}
h4 {
  font-size: 18px;
}
h5 {
  font-size: 16px;
}
h6 {
  font-size: 14px;
}

p {
  font-size: 16px;
  margin-bottom: 10px;
  line-height: 1.75;
}
strong {
  font-weight: bold;
}
em {
  font-style: italic;
}
pre {
  font-family: 'Courier New', Courier, monospace;
  font-size: 13px;
  font-weight: bold;
}

ul,
ol {
  margin: 0 0 10px 0;
}

img {
  width: auto;
  max-width: 100%;
}

.inner {
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
}

.custom-class.after {
  position: absolute;
}

.md-figure {
  margin-top: 30px;
  text-align: center;
  figcaption {
    font-style: italic;
  }
}

.jRMtza {
  z-index: 9999999999 !important;
}

sup {
  position: relative;
  top: -0.5em;
  font-size: 60%;
}

input {
  border-radius: 8px;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

input[type='date']::-webkit-datetime-edit {
  color: '#606060' !important;
}

.__react_component_tooltip.show {
  opacity: 1 !important;
}

.__react_component_tooltip {
  max-width: 95vw;
}

.rc-slider-disabled {
  background-color: transparent !important;
}
.__react_component_tooltip.show {
  opacity: 1 !important;
}

.__react_component_tooltip {
  max-width: 95vw;
}

.rc-slider-disabled {
  background-color: transparent !important;
}

.rc-slider-handle {
  &:after {
    content: '';
    position: absolute;
    left: -14px;
    top: -15px;
    width: 40px;
    height: 40px;
    border-radius: 50px;
    z-index: 9999999;
    // background: #e83474;
    /*Other styles*/
  }
}

.rc-slider-mark-text {
  pointer-events: none;
}

.flatpickr-calendar {
  background-color: #2b2b2b !important;
  color: #fff !important;
  border-radius: 16px !important;
  box-shadow: 0px 0px 0px !important;
  .flatpickr-weekday {
    color: #f5f5f5 !important;
  }
  .flatpickr-day {
    color: #cacaca !important;
    font-weight: 600 !important;
    &.selected,
    &:hover {
      background: #c4d600 !important;
      border-color: #c4d600 !important;
      color: #101010 !important;
    }
  }
  .flatpickr-month {
    color: #fff !important;
  }
  .flatpickr-months {
    svg {
      color: #cacaca !important;
      fill: #cacaca !important;
    }
  }
}

.MuiPaper-root {
  background-color: #2b2b2b !important;
  border-radius: 16px !important;
  overflow: hidden;
}
.MuiInputLabel-formControl {
  pointer-events: none !important;
  transform: translate(15px, 9px) !important;
  color: #747474 !important;
}
.MuiFormLabel-filled {
  display: none !important;
}
.MuiTypography-overline, .MuiTypography-h4 {
  color: #fff !important;
}
.MuiPickersToolbar-penIconButton, .MuiTypography-overline {
  display: none !important;
}
.MuiDatePickerToolbar-root {
  border: 0px !important;
}
.MuiButtonBase-root {
  color: #c4d600 !important;
}
.MuiCalendarPicker-root {
  background-color: #2b2b2b !important;
  color: #fff !important;
  .Mui-selected {
    background-color: #c4d600 !important;
    color: #101010 !important;
  }
  .MuiPickersDay-root {
    background-color: transparent !important;
    &.Mui-selected {
      background-color: #c4d600 !important;
      color: #101010 !important;
    }
  }
  .MuiButtonBase-root, .MuiTypography-caption {
    color: #fff !important;
  }
  .MuiSvgIcon-root {
    path {
      fill: #fff !important;
    }
  }
}
