$breakpoints: (
'phone-small': 350px,
'phone': 400px,
'phone-wide': 480px,
'phablet': 560px,
'tablet-small': 640px,
'tablet': 768px,
'tablet-wide': 980px,
'tablet-x-wide': 1050px,
'desktop': 1248px,
'desktop-wide': 1440px,
);

@mixin mq($width, $type: min) {
  @if map_has_key($breakpoints, $width) {
    $width: map_get($breakpoints, $width);
    @if $type == max {
      $width: $width - 1px;
    }
    @media (#{$type}-width: $width) {
      @content;
    }
  }
}

@mixin ph($ph) {
  padding-left: $ph;
  padding-right: $ph;
}

@mixin pv($pv) {
  padding-top: $pv;
  padding-bottom: $pv;
}

@mixin mh($mh) {
  margin-left: $mh;
  margin-right: $mh;
}

@mixin mv($mv) {
  margin-top: $mv;
  margin-bottom: $mv;
}

@mixin flex-center() {
  display: flex;
  align-items: center;
}

@mixin flex-start() {
  display: flex;
  align-items: flex-start;
}

@mixin resetformelement() {
  appearance: none;
  border: none;
  outline: none;
  box-shadow: none;
}

@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

@mixin grid-areas {
  body {
    .leftbar { grid-area: leftbar; }
    .rightbar { grid-area: rightbar; }
    .main { grid-area: main;
      header { grid-area: header; }
      #content {grid-area: content; }
    }
  }
}
